.navbarWrapper {
  @apply h-[65px] text-[#ffffff] py-[15px] border-b-2 border-[#973434] bg-red-900 px-[25px];
}

.navbarFlex {
  @apply flex items-center;
}
.navbarLogo {
  @apply text-[24px] font-bold text-[#e3e0e0] mr-auto;
}

.navbarHome,
.navbarGithub {
  @apply text-[#dcdbdb] px-2 text-[18px];
}
@media (max-width: 425px) {
  .navbarWrapper {
    @apply px-[10px];
  }
  
  .navbarLogo {
    @apply text-[22px] 
  }
}

