.photoDisplay-wrapper {
  @apply text-[#ffffff] h-[100%];
}
.photoDisplay-app_name {
  @apply font-thin ml-[87%] text-[11px] p-[5px];
}
.photoDisplay-flex {
  margin: 20px auto;
  @apply w-[60%] border-[2px] border-[#5e1b1b] mb-[20px] h-[300px] rounded-lg py-3 flex flex-col items-center;
}
.photoDisplay-title {
  @apply font-semibold;
}
.photosDisplay-photos {
  @apply flex flex-col justify-between items-center;
}
.photoDisplay-img {
  @apply w-[350px] h-[250px] rounded-md mt-[5px];
}
.photosDisplay-number {
  @apply my-2 bg-[#555] px-6;
}
.photoDisplay-btn_div {
  @apply flex justify-between mt-[20px];
}
.photoDisplay-btn {
  @apply bg-[#681515] py-2 px-10 rounded-md text-[13px];
}
.photoDisplay-btn:hover {
  transition: all 0.3s;
  @apply bg-[#cb2929] py-2 px-10 rounded-md text-[13px];
}
.photoDisplay-enjoy_btn {
  @apply border-t-2 border-b-2 text-[#555] text-[12px];
}
@media (max-width: 320px) {
  .photoDisplay-btn {
    @apply py-2 px-7 text-[12px];
  }
}
@media (max-width: 425px) {
  .photoDisplay-app_name {
    @apply ml-[79%];
  }
  .photoDisplay-flex {
    @apply w-[80%] border-[2px] border-[#5e1b1b] mb-[20px] h-[300px] rounded-lg py-3 flex flex-col items-center;
  }
  .photoDisplay-btn:hover {
    transition: all 0.3s;
    @apply py-1 px-7 text-[12px];
  }
}
