.appWrapper{
    margin: 0 auto;
    @apply max-w-[70%] bg-[#111111] h-[100%]
}
.appNotQualify{
  @apply text-red-900 text-center text-[24px] mt-5
}
  .appFlex {
    @apply text-[#ffffff] flex flex-col justify-between py-[40px] px-[20%] items-center;
  }
  .appBirth-year {
    @apply italic text-[18px];
  }
  .appBirth-input {
    @apply rounded-sm w-[70%] text-[16px] outline-none bg-[#2e2e2e] py-2 pl-3 pr-1 text-[#ffffff] mt-[10px]
  }
  .appSubmit-btn{
      @apply bg-green-900 py-2 px-8 rounded-md mt-[30px]
  }
  .appSubmit-btn:hover{
    transition: all 0.3s;
    @apply bg-green-500
}
@media (max-width: 600px) {
  .appWrapper{
    @apply max-w-[100%] h-[100%]
}
.appBirth-year {
  @apply mt-[20px]
}
.appBirth-input {
  @apply rounded-sm w-[100%] mt-[30px]
}
.appSubmit-btn{
    @apply mt-[50px]
}
}